.header{
    height: 60px;
    display: flex;
    align-items: center;
    background-color: #131921;
    position: sticky;
    top: 0;
    z-index: 100;
}
.header_logo{
    color: white;
    width: 100px;
    object-fit: contain;
    margin: 0 20px;
    margin-top: 18px;
}

.header_search{
    display: flex;
    flex: 1 ;
    align-items: center;
    border-radius: 24px;
    

}
.header_searchInput{
    height: 22px;
    padding: 10px !important;
    border: none;
    width: 100%;

}

.header_searchIcon{
    padding: 5px;
    height: 22px !important;
    background-color: #cd9042;

}
.header_nav{
    display: flex;
    justify-content: space-evenly;
}

.header_option{
    display: flex;
    flex-direction: column;
    margin-left: 5px;
    margin-right: 5px;
    color: white;
}
.header_optionLineOne{
    font-size: 10px;

}
.header_optionLineTwo{
    font-size: 13px;
    font-weight: 800;

}

.header_optionaBascket{
    display: flex;
    align-items: center;
    color: white;

}
.backetCount{
    margin-right: 10px;
    margin-left: 10px;
}